<template>
    <div class="article-page">
      <div class="article-header">
        <h1 class="article-title">{{ article.title }}</h1>
        <div class="article-meta">
          <span class="article-author" v-if="article.author">{{ article.author }}</span>
          <span class="article-date">{{ formatDate(article.createdAt) }}</span>
        </div>
      </div>

      <div class="article-content">
        <p>{{ article.content }}</p>
      </div>

      <div class="article-footer">
        <span class="article-views">浏览: {{ article.views }}次</span>
      </div>
    </div>
  </template>

<script>
export default {
  name: 'ArticlePage',
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  methods: {
    formatDate (dateString) {
      const date = new Date(dateString)
      return date.toLocaleDateString()
    }
  }
}
</script>

  <style scoped>
  .article-page {
    padding: 15px;
    background-color: #fff;
  }

  .article-header {
    margin-bottom: 20px;
  }

  .article-title {
    font-size: 1.6em;
    font-weight: bold;
    color: #333;
  }

  .article-meta {
    font-size: 0.9em;
    color: #777;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }

  .article-author {
    color: #3b82f6;
  }

  .article-date {
    color: #aaa;
  }

  .article-content {
    font-size: 1em;
    color: #555;
    line-height: 1.6;
    margin-top: 15px;
  }

  .article-footer {
    margin-top: 20px;
    font-size: 0.9em;
    color: #888;
  }

  .article-views {
    font-weight: bold;
  }
  </style>
