<template>
    <div class="comment-card">
      <!-- 左侧头像和用户名 -->
      <div class="user-info" @click="onClickUserInfo">
        <img
          :src="comment.userAvatar"
          alt="User Avatar"
          class="user-avatar"
          @click="handleAvatarClick"
        />
        <span class="username">{{ comment.username }}</span>
      </div>
      <!-- 右侧评论内容 -->
      <div class="comment-content">
        <p class="content">{{ comment.content }}</p>
        <span class="timestamp">{{ formatDate(comment.createdAt) }}</span>
        <span class="timestamp delete-btn" v-if="enableDelete" @click="onClickDelete">删除</span>
      </div>
    </div>
  </template>

<script>
export default {
  name: 'CommentCard',
  props: {
    comment: {
      type: Object,
      required: true
    },
    enableLink: {
      type: Boolean,
      required: false,
      default: false
    },
    enableDelete: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  methods: {
    // 点击头像的事件处理
    handleAvatarClick () {
      this.$emit('avatar-click', this.comment.userId)
    },
    // 格式化日期
    formatDate (dateString) {
      const options = { year: 'numeric', month: 'long', day: 'numeric', hour: '2-digit', minute: '2-digit' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    },
    onClickUserInfo () {
      if (this.enableLink === false) {
        return
      }
      this.$router.push(`/userProfile?userId=${this.comment.userId}`)
    },
    onClickDelete () {
      this.$emit('clickDelete', this.comment.commentId)
    }
  }
}
</script>

  <style scoped>
  .comment-card {
    display: flex;
    flex-direction: row;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin: 10px 0;
    align-items: flex-start;
    background-color: #f9f9f9;
  }

  .user-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 15px;
  }

  .user-avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
    object-fit: cover;
  }

  .username {
    margin-top: 5px;
    font-size: 14px;
    color: #555;
  }

  .comment-content {
    flex: 1;
  }

  .content {
    font-size: 16px;
    color: #333;
    word-break: break-word;
  }

  .timestamp {
    display: block;
    margin-top: 5px;
    font-size: 12px;
    color: #999;
  }
  .delete-btn{
    float: right;
    font-size: 15px;
    color: blue;
    font-weight: bold;
    color: #c4090957;
  }
  </style>
