<script>
import { getArticleById } from '@/api/article'
import { getUserById } from '@/api/user'
import ArticlePage from '@/components/ArticlePage.vue'
import userImg from '@/components/userImg.vue'
import { hasLiked, likeCount, unlikeArticle, likeArticle } from '@/api/articleLike'
import { getUserId } from '@/util/local'
import { getComments, postComment, deleteComment } from '@/api/comment'
import commentCard from '@/components/CommentCard.vue'
import commentInput from '@/components/CommentInput.vue'
import { Notify } from 'vant'
export default {
  name: 'passage_display_vue',
  components: {
    ArticlePage,
    userImg,
    commentCard,
    commentInput
  },
  mounted () {
  },
  async created () {
    const res = await getArticleById(this.$route.query.id)
    this.article = res.data
    const { data: user } = await getUserById(this.article.authorId)
    console.log(user)
    this.user = user
    await this.onLike()
    const commentRes = await getComments(this.article.articleId)
    this.comments = commentRes.data
    if (getUserId() === this.article.authorId) {
      this.enableDelete = true
    }
  },
  data: () => {
    return {
      like: {
        hasLiked: false,
        likes: 0,
        likeId: 0
      },
      localUserId: getUserId(),
      article: {},
      user: {},
      showShare: false,
      likeCount: 0,
      comments: [],
      commentContent: '',
      enableDelete: false,
      options: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '朋友圈', icon: 'wechat-moments' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' }
        ],
        [
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' },
          { name: '小程序码', icon: 'weapp-qrcode' }
        ]
      ]
    }
  },
  methods: {
    async onClickLike () {
      if (getUserId() === '') {
        Notify({
          type: 'warning',
          message: '请先登录'
        })
        return
      }
      const res = await hasLiked(this.localUserId, this.article.articleId)
      this.like.likeId = res.data
      if (res.data === 0) {
        this.like.hasLiked = false
      } else {
        this.like.hasLiked = true
      }
      this.like.likeId = res.data
      if (this.like.hasLiked === true) {
        await unlikeArticle(this.like.likeId)
      } else {
        await likeArticle(this.localUserId, this.article.articleId)
      }
      const { data: count } = await likeCount(this.article.articleId)
      this.like.likes = count
    },
    async onLike () {
      const res = await hasLiked(this.localUserId, this.article.articleId)
      this.like.likeId = res.data
      if (res.data === 0) {
        this.like.hasLiked = false
      } else {
        this.like.hasLiked = true
      }
      const { data: count } = await likeCount(this.article.articleId)
      this.like.likes = count
    },
    // 提交评论
    async handleCommentSubmit (content) {
      // 判断用户是否登录
      if (getUserId() === '') {
        Notify({
          type: 'warning',
          message: '请先登录'
        })
        return
      }
      const res = await postComment(this.article.articleId, this.localUserId, content)
      this.comments = res.data
    },
    // 删除评论
    async onDeleteComment (commentId) {
      const res = await deleteComment(commentId)
      if (res.code === 200) {
        Notify({
          type: 'success',
          message: '删除评论'
        })
        const commentRes = await getComments(this.article.articleId)
        this.comments = commentRes.data
        return
      }
      Notify({
        type: 'danger',
        message: '删除失败'
      })
    }
  },
  watch: {
  }
}
</script>
<template>
    <div id="passage_display_vue">
      <van-nav-bar
        title="文章浏览"
        left-text="返回"
        left-arrow
        @click-left="$router.go(-1)"
        />
        <ArticlePage :article="article" class="animate__animated animate__fadeInDown"/>
        <div class="like" >
          <van-button icon="star-o" type="default" color="#ffaaa5"
          ref="like-btn"
          class="animate__animated animate__lightSpeedInRight"
           @click="onClickLike">{{like.likes}}赞
          </van-button>
          <userImg :user="user" :enableLink="true" />
        </div>
        <div class="share">
          <van-cell title="分享给朋友" @click="showShare = true" />
          <van-share-sheet
          v-model="showShare"
          title="立即分享给好友"
          :options="options"
        />
        </div>
        <commentInput :modelValue="commentContent" @submit="handleCommentSubmit" class="animate__animated animate__flipInY"/>
        <commentCard v-for="comment in comments"
        :key="comment.commentId" :comment="comment"
        :enable-link="true"
        @clickDelete="onDeleteComment"
        :enable-delete="enableDelete"/>
    </div>
</template>
<style scoped>
.van-button{
  position: absolute;
  right: 0;
}
</style>
