import request from '@/util/request'
// 获取所有评论
export function getComments (articleId) {
  return request.get(`/comment/article/${articleId}`)
}
// 发布评论
export function postComment (articleId, userId, content) {
  return request.post('/comment/post', {
    articleId, userId, content
  })
}
// 删除评论
export function deleteComment (commentId) {
  return request.get(`/comment/delete/${commentId}`)
}
