<template>
    <div class="comment-input">
      <!-- 评论输入框 -->
      <textarea
        v-model="currentContent"
        class="textarea"
        :class="{ focused: isFocused }"
        placeholder="写下你的评论..."
        @focus="handleFocus"
        @blur="handleBlur"
      ></textarea>
      <!-- 提交按钮 -->
      <button
        class="submit-btn"
        :disabled="!currentContent.trim()"
        @click="submitComment"
      >
        发布评论
      </button>
    </div>
  </template>

<script>
export default {
  name: 'CommentInput',
  props: {
    modelValue: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      isFocused: false, // 输入框是否聚焦
      currentContent: this.modelValue // 本地双向绑定
    }
  },
  watch: {
    // 监听 modelValue 变化
    modelValue (newValue) {
      this.currentContent = newValue
    },
    // 双向绑定 currentContent
    currentContent (newValue) {
      this.$emit('update:modelValue', newValue)
    }
  },
  methods: {
    handleFocus () {
      this.isFocused = true
    },
    handleBlur () {
      this.isFocused = false
    },
    submitComment () {
      if (this.currentContent.trim()) {
        this.$emit('submit', this.currentContent.trim())
        this.currentContent = '' // 清空内容
      }
    }
  }
}
</script>

  <style scoped>
  .comment-input {
    display: flex;
    flex-direction: column;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    background-color: #f9f9f9;
    transition: box-shadow 0.3s ease, border-color 0.3s ease;
  }

  .textarea {
    width: 100%;
    height: 100px;
    border: none;
    outline: none;
    resize: none;
    padding: 10px;
    font-size: 14px;
    border-radius: 5px;
    background-color: #fff;
    transition: box-shadow 0.3s ease, border-color 0.3s ease;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
    width:95%;
  }

  .textarea.focused {
    border: 2px solid #42b983;
    box-shadow: 0 0 8px rgba(66, 185, 131, 0.4);
  }

  .submit-btn {
    margin-top: 10px;
    align-self: flex-end;
    padding: 8px 15px;
    background-color: #42b983;
    color: #fff;
    font-size: 14px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .submit-btn:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }

  .submit-btn:not(:disabled):hover {
    background-color: #36a672;
  }
  </style>
