<template>
    <div class="user-profile">
      <!-- 用户头像 -->
      <div class="avatar-wrapper animate__animated animate__zoomIn">
        <img :src="user.avatar" alt="User Avatar" class="avatar" @click="onClickUserAvatar"/>
      </div>

      <!-- 用户名 -->
      <div class="username">
        <span>作者:{{ user.username }}</span>
      </div>
    </div>
  </template>

<script>
export default {
  name: 'UserProfile',
  props: {
    user: {
      type: Object,
      required: true
    },
    enableLink: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  methods: {
    onClickUserAvatar () {
      if (this.enableLink === false) {
        return
      }
      this.$router.push(`/userProfile?userId=${this.user.userId}`)
    }
  }
}
</script>

  <style scoped>
  .user-profile {
    text-align: center;
    padding: 15px;
    background-color: #fff;
  }

  .avatar-wrapper {
    width: 80px;
    height: 80px;
    margin: 0 auto;
    border-radius: 50%;
    overflow: hidden;
  }

  .avatar {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .username {
    margin-top: 10px;
    font-size: 1.2em;
    color: #333;
  }
  </style>
