import request from '@/util/request'
// 点赞
export function likeArticle (userId, articleId) {
  return request.post('article-like/like', {
    userId, articleId
  })
}
// 是否点赞
export function hasLiked (userId, articleId) {
  return request.post('/article-like/has-liked', {
    userId, articleId
  })
}
// 取消点赞
export function unlikeArticle (likeId) {
  return request.get('article-like/unlike', {
    params: {
      likeId
    }
  })
}
// 获取点赞总数
export function likeCount (articleId) {
  return request.get('article-like/like-count', {
    params: {
      articleId
    }
  })
}
